import { Navbar,Nav,NavDropdown,Form,Button,Jumbotron,FormControl } from 'react-bootstrap';
import React, { useState } from 'react';
import '../App.css';
import Data from './data.js';
import Detail from './Detail.js'
import axios from 'axios';


import { Link, Route, Switch } from 'react-router-dom';

function App() {

  let [shoes, setShoes] = useState(Data);

  let [score, setScore] = useState([10,11,12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">shoose shop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Switch>

      <Route exact path="/">

      <Jumbotron className="background">
        <h1>20% season oFF</h1>
        <p>
          This is a simple hero unit, a simple jumbotron-style component for calling
          extra attention to featured content or information.
        </p>
        <p>
          <Button variant="primary">Learn more</Button>
        </p>
      </Jumbotron>

      <div className="container">
        <div className="row">
          {shoes.map((item,idx)=>{
            return (
              <Card shoes={shoes[idx]} i={idx} key={idx}/>
            )
          })}
        </div>
        <button className="btn btn-primary" onClick={()=>{

          //로딩중 코드 만들기

          //axios.post('서버URL', {id : 'codding_id', pw : 1234 });

          axios.get('https://codingapple1.github.io/shop/data2.json')
          .then((reslut)=>{
          //로딩중 삭제 코드 만들기
            console.log(reslut.data);
            setShoes( [...shoes, ...reslut.data] )

          })
          .catch(() => {
            //로딩중 삭제 코드 만들기
            console.log('실패했어요')
          })
        }}> 더보기 </button>
      </div>

      </Route>
      <Route path="/detail/:id">
        < Detail shoes={shoes} score={score} setScore={setScore}/>
      </Route>

      </Switch>

      {/*<Route path="/:id">
        <div>
          아무거나 적었을때 보여주셈
        </div>
        </Route>*/}


    </div>
  );
}

function Card(props){
  return (
      <div className="col-md-4">
        <img src={"https://codingapple1.github.io/shop/shoes" + ( props.i + 1 ) + ".jpg"} width="100%" />
        <h4> { props.shoes.title}  </h4>
        <p> { props.shoes.content } </p>
        <p> { props.shoes.price } </p>
      </div>
  )
}

export default App;