import React, { useEffect, useState } from 'react';
import { useHistory,useParams } from 'react-router-dom'
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
position: inline-block;
width: 100%;
height: 100px;
  padding : 20px;
`;

let Dam = styled.h4`
  font-size: 30px;
  color : ${ props => props.title }
`;


function Detail(props){
    let { id } = useParams();
    let history = useHistory();
    let select = props.shoes.find(function(choic){
      return choic.id == id
    })
    
    let [alert, setAlert] = useState(true)

    let [inputData,setInputData] = useState('')

    useEffect(() => {
      let Timmer = setTimeout(() => {{ setAlert(false) } }, 2000);
      console.log('안녕');
      return () => { clearTimeout(Timmer)};
    },[alert]);


    return (
      <div className="container">
        <Box>
          <Dam title='gray' className="red">Detail</Dam>
        </Box>
        {inputData}
        <input type="text" onChange={(e)=>{
            setInputData(e.target.value)
        }} />
        
        { alert === true
        ?   (<div className="my-alert my-alert2">
              <p>재고가 얼마 남지 않았습니다.</p>
            </div>)
        : null
        }

            <div className="row">
              <div className="col-md-6">
                <img src={"https://codingapple1.github.io/shop/shoes" + id + ".jpg"} width="100%" />
              </div>
              <div className="col-md-6 mt-4">
                <h4 className="pt-5">{select.title}</h4>
                <p>{select.content}</p>
                <p>{select.price}</p>

                <Info score={props.score}></Info>

                <button className="btn btn-danger" onClick={()=>{
                  props.setScore([9,10,11])
                }}>주문하기</button> 
                <button className="btn btn-danger" onClick={()=>{
                    //history.goBack();
                    history.push('/')
                }}>뒤로가기</button> 
              </div>
            </div>
          </div> 
    )
  }

  function Info(props){
    return(
      <p>재고: {props.score[0]} </p>
    )
  }


  export default Detail;